@import url('https://fonts.googleapis.com/css2?family=Prompt&display=swap');

*{
  box-sizing: border-box;
  font-family: 'Prompt',sans-serif;
}
body{
  background: #f7f7f7;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  margin: 0;
}
.container{
  margin: 30px auto;
  width: 350px;
}
.horizontal-menu{
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background: #333;
  text-align: center;
}
.horizontal-menu li{
  display:inline-block;
}
.horizontal-menu a{
  color: #f7f7f7;
  text-decoration: none;
  padding: 16px;
  display: block;
}
.horizontal-menu>li{
  flex: 1;
  text-align: center;
}
.horizontal-menu>li:first-of-type{
  border-right: 3px solid #ededed;
}